<template>
    <div class="health">
        <div class="title-content clearfix">
            <div class="title-box fl">
                <div class="t-text">健康>>></div>
            </div>
            <div class="line-box fl"></div>
            <div class="line-box fl"></div>
            <div class="ad-img-box fr">
                <a target="_blank" href="https://g.163.com/c?AID=259321&amp;FlightID=2057&amp;Values=339870558&amp;Redirect=http://data.163.com/special/datablog/">
                    <img class="ad-img auto-img" src="https://yt-adp.ws.126.net/channel4/1200125_roku_20190313.jpg" border="0"  alt="">
                </a>
            </div>
        </div>
        <!-- <div class="banner-content clearfix">
            <div class="banner-box fl">
                <div class="block">
                    <el-carousel height="400px">
                        <el-carousel-item v-for="(item,index) in HealthData.slice(0,6)" :key="index" >
                            <img class="imgs auto-img" :src="item.images" alt="" @click="goDetail(item.id)">
                        </el-carousel-item>
                    </el-carousel>
                </div>
            </div>
            <div class="cart-box fl ">
                <div class="cart-item" v-for="(item,index) in HealthData.slice(8,10)" :key="index" @click="goDetail(item.id)">
                    <img class="imgs" :src="item.images" alt="">
                    <div class="text">{{item.title}}</div>
                </div>
            </div>
        </div> -->
        <div class="newList-content">
            <div class="clearfix">
                <div class="new-list-left-box fl">
                     <div class="list-item clearfix" v-for="(item,index) in HealthData" :key="index" @click="goDetail(item.id)">
                        <div class="left-img fl">
                            <el-image class=" err-imgs">
                                <div slot="error" class="image-slot">
                                    <i class="el-icon-picture-outline"></i>
                                </div>
                            </el-image>
                            <img class="a-img auto-img" :src="item.images" alt="">
                        </div>
                        <div class="right-cont fl">
                            <div class="title">{{item.title}}</div>
                            <div class="text-cont">
                                <span class="post-name">{{item.posterNickName}}</span>
                                <span class="pub-time">日期：{{item.publishTime}}</span>
                                <span class="comment-count">评论：{{item.commentCount}}</span>
                            </div>
                        </div>
                    </div>

                     <div class="pagination-page">
                        <el-pagination 
                        class="pag-box" 
                        background 
                        layout="prev, pager, next" 
                        :size-change="10"
                        :total="1000"
                        :current-change = "currentPage"
                        @current-change="handleCurrentChange"
                        >
                        </el-pagination>
                    </div>
                   
                </div>

                <div class="new-other-right-box fl">
                    <div class="other-list">
                        <div class="other-box-one">
                            <el-carousel height="280px">
                                <el-carousel-item v-for="(item,index) in healthAdData" :key="index" >
                                    <img class="imgs auto-img" :src="item" alt="">
                                </el-carousel-item>
                            </el-carousel>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import "../../assets/css/health.less";
    export default {
        name:'health',
        data(){
            return{
                currentPage:1,
                // 定义页数
                pageNum:'',
                HealthData:[],
                healthAdData:[
                    'https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=3759539346,2589972511&fm=26&gp=0.jpg',
                    'https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=2278559627,4028995888&fm=26&gp=0.jpg',
                    'https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=1034183066,727508731&fm=26&gp=0.jpg',
                ],
            }
        },
        created(){
            this.getHealthData()
        },
        methods:{

            getHealthData(pageNum){
                this.axios({
                    method:'GET',
                    url:'/list',
                    params:{
                        apikey: this.appkey,
                        category:'T1414389941036',
                        page:pageNum,
                    }
                }).then((result) =>{
                    // console.log('result',result)
                    this.HealthData = result.data.data
                }).catch((err) =>{
                    // console.log('err',err)
                })
            },
             //点击页码，获取对应的val值，在传给要显示的页数内容
            handleCurrentChange(val){
                
                // console.log('val',val)
                let pageNum = val;
                // console.log('pageNum',pageNum)
                //调用请求数据，才能获取值
                this.getHealthData(pageNum)
            
            },
             goDetail(id) {
                // console.log(id);
                this.$router.push({ name: "Detail", query: { id } });
            },
        }
    }
</script>

